@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
/* importing a font type named rubik from google font APIs  */


/* {index.css is the global level where you can conrol all syting of your page}; */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  
}

/* Here i can remove the underlying that exist for every clickable teamName by passing  */
a {
  text-decoration: none;
}




body {
 
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
  background-color: #282c34;
  color: white;

  max-width: 1100px;
  margin: 0 auto;
  
}

a{
  color: lightgray;
}



.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}
.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detial{text-align:right}.MatchDetailCard .additional-detial>p{margin-bottom:20px}
.MatchSamllCard{padding:10px;margin-bottom:20px}.MatchSamllCard .match-result{margin-top:10px}
.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name{font-size:4rem;margin:20px}.TeamPage .win-loss-section{justify-self:center;padding:40px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{align-self:center;justify-self:center;font-size:1.5rem}
.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .YearSelector{justify-self:end;padding:20px}.MatchPage .page-heading{margin:20px}
.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}
.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}
.TeamTile{height:170px;text-align:center;border:1px solid #fff;padding:20px}
